<template>
  <div
    v-on:mouseover="hoverCard = false"
    v-on:mouseout="hoverCard = true"
    class="main"
  >
    <div
      class="secondDiv"
      v-bind:class="{ hidden: second }"
      v-on:click="
        (input = false), (second = true), (span = true), focusInput('myInput')
      "
    >
      <p>
        {{ card_data.title }}
      </p>
      <!-- <span v-bind:class="{ hidden: hoverCard }" ref="close">&#10006;</span> -->
    </div>
    <div
      v-bind:class="{ hidden: span }"
      class="crest"
      v-on:click="deleteCard()"
    >
      <span v-bind:class="{ hidden: hoverCard }">&#10006;</span>
    </div>
    <!-- <div class="third" v-bind:class="{ hidden: input }"> -->
    <input
      v-bind:class="{ hidden: input }"
      type="text"
      v-model="title"
      v-on:blur="(input = true), (second = false), (span = false), Change()"
      v-on:keypress.enter="
        (input = true), (second = false), (span = false), Change()
      "
      ref="myInput"
    />
    <!-- </div> -->
  </div>
</template>
<script>
import { mapActions } from 'vuex';

export default {
  mounted() {},
  name: 'Card',
  // mounted() {
  //   const
  // },
  data() {
    return {
      hoverCard: true,
      second: false,
      input: true,
      span: false,
      title: this.card_data.title,
    };
  },
  props: {
    card_data: {
      type: Object,
      default() {
        return {};
      },
    },
    list: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  methods: {
    focusInput(inputRef) {
      // $refs is an object that holds the DOM references to your inputs
      this.$refs[inputRef].select();
    },
    ...mapActions(['DELETE_CARD', 'GET_BOARD', 'RENAME_CARD']),
    Change() {
      if (this.title !== '') {
        this.RENAME_CARD({
          boardId: this.$route.params.board_id,
          cardId: this.card_data.id,
          data: {
            title: this.title,
            list_id: this.list.id,
          },
        });
        this.GET_BOARD(this.$route.params.board_id);
      }
    },
    deleteCard() {
      this.DELETE_CARD({
        boardId: this.$route.params.board_id,
        cardId: this.card_data.id,
      });
      this.GET_BOARD(this.$route.params.board_id);
    },
    showInput() {
      const input = document.querySelector('#input');
      input.type = 'text';
      input.select();
    },
    // hideInput() {
    //   const input = document.querySelector('#card');
    //   input.type = 'hidden';
    // },
  },
};
</script>
